<template>
  <ztree
    :setting="{
      async:{
        url: './data.json'
      },
      data: {
        key: {
            name: 'label'
        }
      }
    }"
    :data="[
    {
      label: '父节点1 - 展开',
      open: true,
      children: [
        {
          label: '父节点11 - 折叠',
          children: [
            {
              label: '叶子节点111'
            },
            {
              label: '叶子节点112'
            },
            {
              label: '叶子节点113'
            },
            {
              label: '叶子节点114'
            }
          ]
        },
        {
          label: '父节点12 - 折叠',
          children: [
            {
              label: '叶子节点121'
            },
            {
              label: '叶子节点122'
            },
            {
              label: '叶子节点123'
            },
            {
              label: '叶子节点124'
            }
          ]
        },
        {
          label: '父节点13 - 没有子节点',
          isParent: true
        }
      ]
    }
  ]"
  >
  </ztree>
</template>
<script>
	import ztree from "ztreev";
	export default {
		components: {
			ztree
		}
	};
</script>
<style>
  @import "~ztree/css/metroStyle/metroStyle.css";
</style>


